<template>
  <div id="preview">
    <!--preview.vueeditor.vueapp.vueeditorpreview-->
    <!--{{profile.name}}-->
    <div class="profile">
      <h2></h2>
      <p>{{`` + resume.profile.name || ``}}</p>
      <p>{{`` + resume.profile.city || ``}}</p>
      <p>{{`` + resume.profile.birth || ``}}</p>
    </div>
    <div class="work" v-if="filter(resume.workExperience).length > 0">
      <h2></h2>
      <ul>
        <li v-for="work in filter(resume.workExperience)">
          <p>{{`` + work.company}}</p>
          <p>{{`` + work.content}}</p>
        </li>
      </ul>
    </div>
    <div class="study" v-if="filter(resume.studyExperience).length > 0">
      <h2></h2>
      <ul>
        <li v-for="study in filter(resume.studyExperience)">
          <p>{{`` + study.school}}</p>
          <p>{{`` + study.duration}}</p>
          <p>{{`` + study.degree}}</p>
        </li>
      </ul>
    </div>
    <div v-if="filter(resume.projectExperience).length > 0" class="project">
      <h2></h2>
      <ul>
        <li v-for="pro in filter(resume.projectExperience)">
          <p>{{`` + pro.name}}</p>
          <p>{{`` + pro.content}}</p>
        </li>
      </ul>
    </div>
    <div v-if="filter(resume.awardExperience).length > 0" class="award">
      <h2></h2>
      <ul>
        <li v-for="award in filter(resume.awardExperience)">
          <p>{{``+award.name}}</p>
        </li>
      </ul>
    </div>
    <div class="contacts">
      <h2></h2>
      <p>{{`QQ` + resume.contacts.qq || `QQ`}}</p>
      <p>{{`` + resume.contacts.wechat || `wexin`}}</p>
      <p>{{``+resume.contacts.email || `email`}}</p>
      <p>{{``+resume.contacts.phone || `phone`}}</p>
    </div>
  </div>
</template>
<script>
  export default {
    //editor
//    props: ['profile','workExperience','studyExperience','projectExperience','awardExperience','contacts'],
    props: ['resume'],
    methods: {
      isEmpty (obj) { //value
        let empty = true;
        for (let key in obj) {
          if (obj[key]) {
            empty = false;
            break;
          }
        }
        return empty
      },
      filter(arr) {
        return arr.filter( (item)=> {return !this.isEmpty(item)} )
      }
    }
  }
</script>
<style>
  #preview {
    min-height: 100px;
    padding: 32px;
    text-align: left;
    line-height: 2.5em;
    overflow: auto;
  }
  #preview .profile>h2,.project>h2,.work > h2,.award>h2,.study>h2,.contacts>h2 {
    font-size: 22px;
    border: none;
    padding: 5px;
    border-bottom: 1px solid black;
    color: #6b8afc;
  }
</style>



