  <div>
    <h2></h2>
    <el-form>
      <el-form-item label="">
        <el-input v-model="profile.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="profile.city"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="profile.birth"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    props: ['profile'], //profile
  }
</script>



